"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card'
import { useUserStore } from '@/store/user-store'

const MOOD_OPTIONS = [
  { value: 'good', label: 'Â•ΩÂøÉÊÉÖ' },
  { value: 'neutral', label: '‰∏ÄËà¨' },
  { value: 'bad', label: '‰∏çÂ§™Â•Ω' },
]

const COMMON_TAGS = [
  'Â∑•‰Ωú', 'Â≠¶‰π†', 'ËøêÂä®', 'Â®±‰πê', 'ÂÆ∂Â∫≠', 'Á§æ‰∫§', '‰ºëÊÅØ', 'ÂèçÊÄù'
]

export default function LogForm() {
  const { addTimeLog } = useUserStore()
  const [formData, setFormData] = useState({
    content: '',
    mood: 'neutral' as 'good' | 'neutral' | 'bad',
    date: new Date().toISOString().split('T')[0],
    tags: [] as string[],
    customTag: '',
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  const handleTagToggle = (tag: string) => {
    setFormData(prev => {
      const newTags = prev.tags.includes(tag)
        ? prev.tags.filter(t => t !== tag)
        : [...prev.tags, tag]
      return { ...prev, tags: newTags }
    })
  }

  const handleAddCustomTag = () => {
    if (formData.customTag.trim() && !formData.tags.includes(formData.customTag.trim())) {
      setFormData(prev => ({
        ...prev,
        tags: [...prev.tags, prev.customTag.trim()],
        customTag: '',
      }))
    }
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    addTimeLog({
      content: formData.content,
      mood: formData.mood,
      date: new Date(formData.date),
      tags: formData.tags,
    })
    
    // ÈáçÁΩÆË°®Âçï
    setFormData({
      content: '',
      mood: 'neutral',
      date: new Date().toISOString().split('T')[0],
      tags: [],
      customTag: '',
    })
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>ËÆ∞ÂΩï‰ªäÊó•Êó∂Èó¥Êó•Âøó</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="date" className="block text-sm font-medium">
              Êó•Êúü
            </label>
            <input
              id="date"
              name="date"
              type="date"
              value={formData.date}
              onChange={handleChange}
              className="w-full rounded-md border border-input bg-background px-3 py-2"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label htmlFor="content" className="block text-sm font-medium">
              ÂÜÖÂÆπ
            </label>
            <textarea
              id="content"
              name="content"
              value={formData.content}
              onChange={handleChange}
              rows={5}
              placeholder="ËÆ∞ÂΩï‰ªäÂ§©ÁöÑÊÑüÊÉ≥„ÄÅÊî∂Ëé∑ÊàñËÄÖÂèçÊÄù..."
              className="w-full rounded-md border border-input bg-background px-3 py-2"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label htmlFor="mood" className="block text-sm font-medium">
              ‰ªäÊó•ÂøÉÊÉÖ
            </label>
            <select
              id="mood"
              name="mood"
              value={formData.mood}
              onChange={handleChange}
              className="w-full rounded-md border border-input bg-background px-3 py-2"
            >
              {MOOD_OPTIONS.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>
          
          <div className="space-y-2">
            <label className="block text-sm font-medium">
              Ê†áÁ≠æ
            </label>
            <div className="flex flex-wrap gap-2">
              {COMMON_TAGS.map(tag => (
                <Button
                  key={tag}
                  type="button"
                  variant={formData.tags.includes(tag) ? 'default' : 'outline'}
                  size="sm"
                  onClick={() => handleTagToggle(tag)}
                >
                  {tag}
                </Button>
              ))}
            </div>
            
            <div className="flex mt-2">
              <input
                type="text"
                value={formData.customTag}
                onChange={(e) => setFormData(prev => ({ ...prev, customTag: e.target.value }))}
                placeholder="Ê∑ªÂä†Ëá™ÂÆö‰πâÊ†áÁ≠æ"
                className="flex-1 rounded-l-md border border-input bg-background px-3 py-2"
              />
              <Button
                type="button"
                onClick={handleAddCustomTag}
                className="rounded-l-none"
                disabled={!formData.customTag.trim()}
              >
                Ê∑ªÂä†
              </Button>
            </div>
            
            {formData.tags.length > 0 && (
              <div className="mt-2">
                <p className="text-sm text-muted-foreground mb-1">Â∑≤ÈÄâÊ†áÁ≠æ:</p>
                <div className="flex flex-wrap gap-1">
                  {formData.tags.map(tag => (
                    <span
                      key={tag}
                      className="px-2 py-1 text-xs rounded-full bg-primary/10 inline-flex items-center"
                    >
                      {tag}
                      <button
                        type="button"
                        onClick={() => handleTagToggle(tag)}
                        className="ml-1 h-4 w-4 rounded-full bg-muted flex items-center justify-center text-muted-foreground"
                      >
                        ‚úï
                      </button>
                    </span>
                  ))}
                </div>
              </div>
            )}
          </div>
          
          <CardFooter className="px-0 pt-4">
            <Button type="submit">‰øùÂ≠òÊó•Âøó</Button>
          </CardFooter>
        </form>
      </CardContent>
    </Card>
  )
} 